import React from 'react';
import SVG from 'react-inlinesvg';

export default class Notification extends React.PureComponent {

 get icons() {
   return {
     android: require('../../asset/star_24.svg')
   };
 }

  render() {
    return (
      <div className="Notification">
        <div className="Notification__icon">
          <SVG src={this.icons[this.props.type]} />
        </div>
        <div className="Notification__cont">
          <div className="Notification__title">{this.props.title}</div>
          <div className="Notification__caption">{this.props.caption}</div>
        </div>
        <div className="Notification__arrow">
          <SVG src={require('../../asset/arrow.svg')} />
        </div>
      </div>
    )
  }
}


